import React from 'react';
import PropTypes from 'prop-types';
import burgerIngredientsStyles from './burger-ingredients.module.css';
import { Tab } from '@ya.praktikum/react-developer-burger-ui-components';
import BurgerIngredientsSection from './burger-ingredients-section';

const BurgerIngredients = ({ handleOpenModal }) => {

	const bunRef = React.useRef();
  const sauceRef = React.useRef();
  const mainRef = React.useRef();

	const [current, setCurrent] = React.useState('');

  const switchTabs = (value) => {
    setCurrent(value);
    switch (value) {
      case '': {
        bunRef.current.scrollIntoView({ behavior: 'smooth' });
        break;
      }
      case '': {
        sauceRef.current.scrollIntoView({ behavior: 'smooth' });
        break;
      }
      case '': {
        mainRef.current.scrollIntoView({ behavior: 'smooth' });
        break;
      }
      default: {
        bunRef.current.scrollIntoView({ behavior: 'smooth' });
      }
    }
  };

  const handleTabChange = (e) => {
    const container = e.target;
    const containerTop = container.getBoundingClientRect().top;
    const bunTop = bunRef.current.getBoundingClientRect().top;
    const sauceTop = sauceRef.current.getBoundingClientRect().top;
    const mainTop = mainRef.current.getBoundingClientRect().top;
    const offset = [
      { name: '', value: Math.abs(containerTop - bunTop) },
      { name: '', value: Math.abs(containerTop - sauceTop) },
      { name: '', value: Math.abs(containerTop - mainTop) },
    ];
    const closest = offset.sort((a, b) => a.value - b.value)[0].name;
    if (current !== closest) {
      setCurrent(closest);
    }
  };

  return (
		<section className={ burgerIngredientsStyles.container }>
			<h1 className={`${burgerIngredientsStyles.title} 'text text_type_main-large`}> </h1>
			<div className={burgerIngredientsStyles.tabs}>
      	<Tab value="" active={ current === '' } onClick={ switchTabs }>
        	
      	</Tab>
      	<Tab value="" active={ current === '' } onClick={ switchTabs }>
        	
      	</Tab>
      	<Tab value="" active={ current === '' } onClick={ switchTabs }>
        	
      	</Tab>
    	</div>
			<ul className={ burgerIngredientsStyles.list } onScroll={handleTabChange}>
        <BurgerIngredientsSection type='bun' name='' onClick={ handleOpenModal } ref={ bunRef } />
        <BurgerIngredientsSection type='sauce' name=''  onClick={ handleOpenModal } ref={ sauceRef } />
        <BurgerIngredientsSection type='main' name='' onClick={ handleOpenModal } ref={ mainRef }/>
      </ul>
		</section>
  )
}

BurgerIngredients.propTypes = {
	handleOpenModal: PropTypes.func.isRequired
}

export default BurgerIngredients;